import React from "react";
import styled, { keyframes } from "styled-components";

const animateLightBar = keyframes`
	0%,5% {
		transform: scaleY(0) ;
	}
    10%{
        transform: scaleY(1) ;
    }
	90% {
        transform: scaleY(1) ;
	}
	95%,100% {
        transform: scaleY(0) ;
	}
`;
const TextBoxAnimation = keyframes`
   0%,5% {
		transform: translate3d(-80vw,0,0);
	}
    10%{
        transform:  translate3d(-80vw,0,0);
    }
	90% {
        transform:  translate3d(0,0,0);
	}
	95%,100% {
        transform: translate3d(0,0,0);
	}
`;
const TextAnimation = keyframes`
    0%,5% {
		transform:  translate3d(80vw,0,0);
	}
    10%{
        transform:  translate3d(80vw,0,0);
    }
	90% {
        transform:  translate3d(0,0,0);
	}
	95%,100% {
        transform: translate3d(0,0,0);
	}
`;
const BorderShow = keyframes`
    0%{
		opacity:0;
	}
   
	95% {
        opacity:0;
	}
    100%{
        opacity:1;
    }
`;

const Wrapper = styled.div`
  position: relative;
  margin: 1rem auto;
  width: 90vw;
  background: rgba(0, 0, 0, 0.65);
  /* background-color: #00b3ff24; */
  border-radius: 10px;
  :after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    box-shadow: inset 0 0 0.1em #00b3ff, inset 0 0 0.2em #00b3ff,
      inset 0 0 0.4em #00b3ff, 0 0 1em #00b3ff;
    animation: ${BorderShow} 2s linear forwards;
    border: 5px solid #fff;
    border-radius: inherit;
  }
  @media only screen and (min-width: 576px) {
    margin: 0 auto;
  }
  @media only screen and (min-width: 768px) {
    width: 90vw;
  }
  @media only screen and (min-width: 992px) {
    width: 80vw;
  }
`;
const TextBox = styled.div`
  overflow: hidden;
  width: 100%;
  /* transform: translate(300px,0); */
  animation: ${TextBoxAnimation} 2s linear forwards;
  padding: 1rem 1rem;
`;
const H1 = styled.h1`
  font-size: inherit;
  /* white-space: nowrap; */
  color: #fff;
  text-shadow: 0 0 0.1em #00b3ff, 0 0 0.2em #00b3ff, 0 0 0.4em #00b3ff,
    0 0 0.8em #00b3ff, 0 0 1.6em #00b3ff;
  /* transform: translate(-300px,0); */
`;
const P = styled.p`
  font-size: 0.3em;
  line-height: 1;
  color: #fff;
  text-shadow: 0 0 0.1em #00b3ff, 0 0 0.2em #00b3ff, 0 0 0.4em #00b3ff,
    0 0 0.8em #00b3ff, 0 0 1.6em #00b3ff;
`;
const LightBar = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  width: 10px;
  height: 100%;
  border-radius: 10px;
  background-color: #fff;
  z-index: 2;
  box-shadow: 0 0 10px #00b3ff, 0 0 20px #00b3ff, 0 0 40px #00b3ff,
    0 0 80px #00b3ff, 0 0 120px #00b3ff;
  animation: ${animateLightBar} 2s linear forwards;
`;
const Flex = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: ${TextAnimation} 2s linear forwards;
  font-size: 4rem;
  h1 {
    text-align: center;
  }
  p {
    text-align: center;
    margin-bottom: 1rem;
  }

  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: space-around;
    font-size: 5rem;
    > * {
      width: 50%;
    }
    h1,
    p {
      text-align: left;
    }
  }
`;
const FarmImg = styled.img`
  width: 150px;
  height: 150px;

  @media only screen and (min-width: 768px) {
    width: 160px;
    height: 160px;
  }
  @media only screen and (min-width: 992px) {
    width: 190px;
    height: 190px;
  }
`;
function Heading() {
  return (
    <Wrapper>
      <TextBox>
        <Flex>
          <div>
            <H1>
              Reward Mechanism
              <br />
            </H1>
            <P>Activate the Reward Mechanism to generate rewards.</P>
          </div>
          <FarmImg src="/images/bq.png" alt="Xenon Machine Image" />
        </Flex>

        <LightBar />
      </TextBox>
    </Wrapper>
  );
}

export default Heading;
