import React, { useState } from "react";
import styled from "styled-components";

const PopupBox = styled.div`
  position: fixed;
  padding: 4rem 2rem 2rem 2rem;
  min-height: 250px;
  min-width: 300px;
  border: 1px solid ${({ theme }) => theme.colors.primary};
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: ${({ theme }) => theme.colors.background};
  border-radius: 15px;
  color: #fff;
`;
const CloseContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1000;
`;
const Close = styled.div`
  position: absolute;
  top: 5%;
  left: 93%;
  height: 10%;
  width: 0.5%;
  transform: translate(-93%, -5%);
  cursor: pointer;
  :after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    height: 100%;
    width: 100%;
    transform: rotate(45deg);
  }
  :before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    height: 100%;
    width: 100%;
    transform: rotate(-45deg);
  }
`;

function Popup() {
  const [open, setOpen] = useState(true);
  return (
    <div>
      {open && (
        <CloseContainer onClick={() => setOpen(false)}>
          <PopupBox>
            <Close onClick={() => setOpen(false)} />
            <p style={{ textAlign: "center" }}>
              <span style={{ fontSize: "1.5em", color: "red" }}>Xenon Machine V1 Deactivated</span>
              <br />
              <br />
              We have begun to migrate to Xenon Machine V2. Rewards for V1
              have now been turned off. Your staked tokens are still safe, but
              they are not receiving any more rewards now.
              <br />
              <br />
              We recommend you harvest your rewards and unstake your tokens from V1
              and migrate them over to the V2 Farm.
              <br />
              <br />
              Greetings, Staff Team.
            </p>
          </PopupBox>
        </CloseContainer>
      )}
    </div>
  );
}

export default Popup;
