import React, { useEffect, useCallback, useState, Suspense, lazy } from "react";
import { Route, useRouteMatch, useLocation } from "react-router-dom";
import { useDispatch } from "react-redux";
import BigNumber from "bignumber.js";
import { useWeb3React } from "@web3-react/core";
import { Text } from "@pancakeswap-libs/uikit";
import styled from "styled-components";
import { BLOCKS_PER_YEAR, CAKE_POOL_PID } from "config";
import FlexLayout from "components/layout/Flex";
import Page from "components/layout/Page";
import Popup from "components/Popup";
import {
  useFarms,
  usePriceBnbBusd,
  useFarmCakePerBlock,
  usePriceX2PBusd,
  usePriceCakeBusd,
} from "state/hooks";
import useRefresh from "hooks/useRefresh";
import { fetchFarmUserDataAsync } from "state/actions";
import { QuoteToken } from "config/constants/types";

import Heading from "./Heading";
import Nebula from "./Nebula";

import FarmCard, { FarmWithStakedValue } from "./components/FarmCard/FarmCard";

import Divider from "../Pools/components/Divider";

const MIGRATION_STARTED = true;

const ControlContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;

  justify-content: space-between;
  flex-direction: column;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 16px 32px;
  }
`;

const Hero = styled.div`
  position: relative;
  color: ${({ theme }) => theme.colors.textSubtle};
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  max-width: 85%;
  padding: 18px 0 10px 0;
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-size: 16px;
    li {
      margin-bottom: 4px;
    }
  }
  @media (min-width: 576px) {
    flex-direction: row;
    margin: 0;
    max-width: none;
    padding: 48px 0;
  }
  ul {
    margin-bottom: 2rem;
  }
`;
const ToggleWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-left: 10px;

  ${Text} {
    margin-left: 8px;
  }
`;

const LabelWrapper = styled.div`
  > ${Text} {
    font-size: 12px;
  }
`;

const FilterContainer = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px 0px;

  ${({ theme }) => theme.mediaQueries.sm} {
    width: auto;
    padding: 0;
  }
`;

const ViewControls = styled.div`
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  align-items: center;
  width: 100%;

  > div {
    padding: 8px 0px;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    justify-content: flex-start;
    width: auto;

    > div {
      padding: 0;
    }
  }
`;

const FarmVersionLabel = styled.div`
  width: 100%;
  text-align: center;
  color: #fff;
  text-shadow: 0 0 0.1em #00b3ff, 0 0 0.2em #00b3ff, 0 0 0.4em #00b3ff,
    0 0 0.8em #00b3ff, 0 0 1.6em #00b3ff;
`;

const Farms: React.FC = () => {
  const { path } = useRouteMatch();
  const farmsLP = useFarms();
  const rewardPerBlock = useFarmCakePerBlock();
  const cakePrice = usePriceCakeBusd();
  const x2pPrice = usePriceX2PBusd();
  const bnbPrice = usePriceBnbBusd();
  const { account } = useWeb3React();

  const dispatch = useDispatch();
  const { fastRefresh } = useRefresh();
  useEffect(() => {
    if (account) {
      dispatch(fetchFarmUserDataAsync(account));
    }
  }, [account, dispatch, fastRefresh]);

  const activeFarms = farmsLP;

  // /!\ This function will be removed soon
  // This function compute the APY for each farm and will be replaced when we have a reliable API
  // to retrieve assets prices against USD
  const farmsList = useCallback(
    (farmsToDisplay): FarmWithStakedValue[] => {
      const cakePriceVsBNB = new BigNumber(
        farmsLP.find((farm) => farm.pid === CAKE_POOL_PID)?.tokenPriceVsQuote ||
          0,
      );
      const farmsToDisplayWithAPY: FarmWithStakedValue[] = farmsToDisplay.map(
        (farm) => {
          if (!farm.tokenAmount || !farm.lpTotalInQuoteToken) {
            return farm;
          }
          // TODO change this. Cake per block comse from farm.,
          const cakeRewardPerBlock = new BigNumber(rewardPerBlock).times(
            farm.poolWeight,
          );
          const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR);

          // cakePriceInQuote * cakeRewardPerYear / lpTotalInQuoteToken
          const lpTotalInQuoteToken = farm.lpTotalInQuoteToken;

          let apy = cakePriceVsBNB
            .times(cakeRewardPerYear)
            .div(lpTotalInQuoteToken);

          if (
            farm.quoteTokenSymbol === QuoteToken.BUSD ||
            farm.quoteTokenSymbol === QuoteToken.UST
          ) {
            apy = cakePriceVsBNB
              .times(cakeRewardPerYear)
              .div(farm.lpTotalInQuoteToken)
              .times(bnbPrice);
          } else if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
            apy = cakeRewardPerYear.div(farm.lpTokenBalanceMC);
          } else if (farm.quoteTokenSymbol === QuoteToken.X2P) {
            apy = cakeRewardPerYear
              .times(cakePrice)
              .div(farm.lpTokenBalanceMC)
              .div(x2pPrice);
          } else if (farm.dual) {
            const cakeApy =
              farm &&
              cakePriceVsBNB
                .times(cakeRewardPerBlock)
                .times(BLOCKS_PER_YEAR)
                .div(farm.lpTotalInQuoteToken);
            const dualApy =
              farm.tokenPriceVsQuote &&
              new BigNumber(farm.tokenPriceVsQuote)
                .times(farm.dual.rewardPerBlock)
                .times(BLOCKS_PER_YEAR)
                .div(farm.lpTotalInQuoteToken);

            apy = cakeApy && dualApy && cakeApy.plus(dualApy);
          }

          let liquidity = farm.lpTotalInQuoteToken;

          if (!farm.lpTotalInQuoteToken) {
            liquidity = null;
          }
          if (farm.quoteTokenSymbol === QuoteToken.BNB) {
            liquidity = bnbPrice.times(farm.lpTotalInQuoteToken);
          }
          if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
            liquidity = cakePrice.times(farm.lpTokenBalanceMC);
          }
          if (farm.quoteTokenSymbol === QuoteToken.X2P) {
            liquidity = x2pPrice.times(farm.lpTokenBalanceMC);
          }

          return { ...farm, apy, liquidity };
        },
      );

      return farmsToDisplayWithAPY;
    },
    [bnbPrice, farmsLP, cakePrice, rewardPerBlock, x2pPrice],
  );

  const farmsStaked = farmsList(activeFarms);

  const renderContent = (): JSX.Element => {
    return (
      <div style={{ marginTop: "2rem" }}>
        {MIGRATION_STARTED && <Popup />}
        <FlexLayout>
          <Route exact path={`${path}`}>
            {farmsStaked.map((farm) => (
              <FarmCard
                key={farm.pid}
                farm={farm}
                bnbPrice={bnbPrice}
                cakePrice={cakePrice}
                account={account}
                removed={false}
              />
            ))}
          </Route>
          <Route exact path={`${path}/history`}>
            {farmsStaked.map((farm) => (
              <FarmCard
                key={farm.pid}
                farm={farm}
                bnbPrice={bnbPrice}
                cakePrice={cakePrice}
                account={account}
                removed
              />
            ))}
          </Route>
        </FlexLayout>
      </div>
    );
  };

  return (
    <>
      <Nebula />
      {/* <Popup/> */}
      <Hero>
        <div>
          <Heading />
          {/* <Heading as="h1" size="xxl" color="secondary" mb="24px">
            {TranslateString(999, "Farms")}
          </Heading> */}
          {/* <ul>
            <li>
              {TranslateString(
                999,
                "Stake Liquidity Pool (LP) tokens to earn."
              )}
            </li>
          </ul> */}
        </div>
      </Hero>
      <Divider style={{ backgroundColor: "#fff" }} mb />
      <Page style={{ position: "relative" }}>
        {/* <Canvas/> */}
        <ControlContainer>
          <ViewControls>
            <ToggleWrapper />
          </ViewControls>
          <FilterContainer>
            <LabelWrapper />
          </FilterContainer>
        </ControlContainer>
        {renderContent()}
        {MIGRATION_STARTED && (
          <FarmVersionLabel>
            <a href='/#/farmsV2'>
            Xenon Machine V1 is now deactivated. Unstake your tokens and restake them
            at V2.</a>
            <br/>
          </FarmVersionLabel>
        )}
      </Page>
    </>
  );
};

export default Farms;
