import React, { useEffect } from "react";
import * as THREE from "three";
import * as PostProcessing from "postprocessing";
import styled from "styled-components";

let scene;
let camera;
let renderer;
const cloudParticles = [] as any;
let composer;

function resizeCanvasToDisplaySize() {
  const canvas = renderer.domElement;
  // look up the size the canvas is being displayed
  const width = canvas.clientWidth;
  const height = canvas.clientHeight;

  // adjust displayBuffer size to match
  if (canvas.width !== width || canvas.height !== height) {
    // you must pass false here or three.js sadly fights the browser
    renderer.setSize(width, height, false);
    camera.aspect = width / height;
    camera.updateProjectionMatrix();

    // update any render target sizes here
  }
}

const onWindowResize = () => {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
};

const Init = (bg = 0x130c14) => {
  // const Parent = document.querySelector("#canvas");
  scene = new THREE.Scene();
  camera = new THREE.PerspectiveCamera(
    60,
    window.innerWidth / window.innerHeight,
    1,
    1000
  );
  // camera.position.x=-50;
  // camera.position.y=;
  camera.position.z = 1;
  camera.rotation.x = 1.16;
  camera.rotation.y = -0.12;
  camera.rotation.z = 0.27;
  const loadingManager = new THREE.LoadingManager(() => {
    document.getElementById("FOGBG").style.display = "none";
  });
  // const ambient = new THREE.AmbientLight(0x555555);
  // scene.add(ambient);
  // const directionalLight = new THREE.DirectionalLight(0x38edc3);
  // directionalLight.position.set(0, 0, 1);
  // scene.add(directionalLight);
  // const redlight = new THREE.PointLight(0x640101, 50, 450, 1.7);
  // redlight.position.set(200, 300, 100);
  // scene.add(redlight);
  // const purpleLight = new THREE.PointLight(0x771aff, 50, 450, 1.7);
  // purpleLight.position.set(100, 300, 100);
  // scene.add(purpleLight);
  // const greenLight = new THREE.PointLight(0x45ca92, 50, 450, 1.7);
  // greenLight.position.set(100, 300, 100);
  // scene.add(greenLight);
  // const blueLight = new THREE.PointLight(0x792e92, 50, 450, 1.7);
  // blueLight.position.set(300, 300, 200);
  // scene.add(blueLight);

  const ambient = new THREE.AmbientLight(0x555555);
  scene.add(ambient);
  const directionalLight = new THREE.DirectionalLight(0x1fa4e9);
  directionalLight.position.set(0, 0, 1);
  scene.add(directionalLight);

  const purple = new THREE.PointLight(0x170c73, 50, 450, 1.7);
  purple.position.set(200, 300, 100);
  scene.add(purple);
  const blue = new THREE.PointLight(0x0b93de, 50, 450, 1.7);
  blue.position.set(100, 300, 100);
  scene.add(blue);
  const green = new THREE.PointLight(0x1e8282, 50, 450, 1.7);
  green.position.set(300, 300, 200);
  scene.add(green);

  renderer = new THREE.WebGLRenderer({
    canvas: document.querySelector("#canvas"),
  });
  // console.log("width",Parent?.getBoundingClientRect().width);
  //   renderer.setSize(Parent?.getBoundingClientRect().width, Parent?.getBoundingClientRect().height);
  scene.fog = new THREE.FogExp2(bg, 0.001);
  renderer.setClearColor(scene.fog.color);

  //   Parent?.appendChild(renderer.domElement);

  const loader = new THREE.TextureLoader(loadingManager);
  loader.load("/smoke.png", function (texture) {
    //  texture is loaded
    const cloudGeo = new THREE.PlaneBufferGeometry(500, 500);
    const cloudMaterial = new THREE.MeshLambertMaterial({
      map: texture,
      transparent: true,
    });
    for (let p = 0; p < 50; p++) {
      const cloud = new THREE.Mesh(cloudGeo, cloudMaterial);
      cloud.position.set(
        Math.random() * 800 - 300,
        500,
        Math.random() * 450 - 450
      );
      cloud.rotation.x = 1.16;
      cloud.rotation.y = -0.12;
      cloud.rotation.z = Math.random() * 2 * Math.PI;
      cloud.material.opacity = 0.55;
      cloudParticles.push(cloud);
      scene.add(cloud);
    }
  });

  loader.load("/stars.jpeg", (txture) => {
    const textureEffect = new PostProcessing.TextureEffect({
      blendFunction: PostProcessing.BlendFunction.COLOR_DODGE,
      texture: txture,
    });
    textureEffect.blendMode.opacity.value = 0.4;

    const bloomEffect = new PostProcessing.BloomEffect({
      blendFunction: PostProcessing.BlendFunction.COLOR_DODGE,
      kernelSize: PostProcessing.KernelSize.SMALL,
      useLuminanceFilter: true,
      luminanceThreshold: 0.3,
      luminanceSmoothing: 0.75,
    });
    bloomEffect.blendMode.opacity.value = 1.5;

    const effectPass = new PostProcessing.EffectPass(
      camera,
      bloomEffect,
      textureEffect
    );
    effectPass.renderToScreen = true;

    composer = new PostProcessing.EffectComposer(renderer);
    composer.addPass(new PostProcessing.RenderPass(scene, camera));
    composer.addPass(effectPass);

    render();
  });
  //   console.log(renderer.domElement)
};
/* eslint-disable no-param-reassign */
const render = () => {
  resizeCanvasToDisplaySize();
  cloudParticles.forEach((p) => {
    p.rotation.z -= 0.0015;
  });
  composer.render(0.1);
  requestAnimationFrame(render);
};

const Canvas = styled.canvas`
  position: fixed;
  top: 80px;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  min-width: 100vw;
  min-height: 100vh;
  z-index: -1;
`;
const BG = styled.div`
  position: fixed;
  top: 80px;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: url("/fog.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
`;
function Nebula() {
  useEffect(() => {
    // window.addEventListener("resize", resizeCanvasToDisplaySize, false);
    Init();
    return () => {
      //   window.removeEventListener("resize", resizeCanvasToDisplaySize, false);
    };
  }, []);

  return (
    <>
      <Canvas id="canvas" />
      <BG id="FOGBG" />
    </>
  );
}

export default Nebula;
